import { createRouter, createWebHistory } from 'vue-router'

const routes = [
  { path: '/', name: 'Home', component: () => import('../views/Home.vue') },
  { path: '/about', name: 'About', component: () => import('../views/About.vue') },
  { path: '/presentation', name: 'Presentation', component: () => import('../views/Presentation.vue') },
  { path: '/organizations', name: 'Organizations', component: () => import('../views/Organizations.vue') },
  { path: '/vuepassword', name: 'vuepassword-view', component: () => import('../views/vuepassword.vue') },
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

export default router
